  import ('@/pages/redirect')
Vue.use(Router)

const router = new Router({

  mode: 'history',
  routes: [{
    path: '/landing',
    name: 'landing',
    component: Landing,
    meta: {
      title: 'Closer'
    }
  }, {
    path: '/over',
    name: 'activityOver',
    component: ActivityOver,
    meta: {
      title: 'Closer'
    }
  }, {
    path: '/pullnew',
    name: 'pullNew',
    component: PullNewIndex,
    meta: {
      title: ''
    }
  }, {
    path: '/pullNew/rule',
    name: 'pullNewRule',
    component: PullNewRule,
    meta: {
      title: ''
    }
  }, {
    path: '/local',
    name: 'localIndex',
    component: LocalIndex,
    meta: {
      title: ''
    }
  }, {
    path: '/local/answer',
    name: 'localAnswer',
    component: LocalAnswer,
    meta: {
      title: ''
    }
  }, {
    path: '/local/result',
    name: 'localResult',
    component: LocalResult,
    meta: {
      title: ''
    }
  }, {
    path: '/local/share',
    name: 'localShare',
    component: LocalShare,
    meta: {
      title: ''
    }
  }, {
    path: '/local/rule',
    name: 'localRule',
    component: LocalRule,
    meta: {
      title: ''
    }
  }, {
    path: '/local/rank',
    name: 'localRank',
    component: LocalRank,
    meta: {
      title: ''
    }
  }, {
    path: '/moon',
    name: 'moonIndex',
    component: MoonIndex,
    meta: {
      title: ''
    }
  }, {
    path: '/moon/rule',
    name: 'moonRule',
    component: MoonRule,
    meta: {
      title: ''
    }
  }, {
    path: '/moon/rank',
    name: 'moonRank',
    component: MoonRank,
    meta: {
      title: ''
    }
  }, {
    path: '/moon/share',
    name: 'moonShare',
    component: MoonShare,
    meta: {
      title: ''
    }
  }, {
    path: '/redirect',
    name: 'redirect',
    component: Redirect
  }]

})
router.beforeEach(({
  meta,
  path,
  fullPath,
  query,
  name,
  params
}, from, next) => {
  document.title = meta.title ? meta.title : ''
    // console.log('router-before:', name, window.ENV.app, window.ENV.wx, query)
  if (name == 'localIndex' || name == 'moonIndex') {
    if (window.ENV.app) {
      // console.log("closer device")
      Cookies.remove('user'); //appuserAPP
      next();
    } else if (window.ENV.wx) {
      if (query.code) {
        next();
        return;
      }
      // console.log('fullpath:', fullPath, query)

      // let _path = (location.origin.indexOf('local.tiejin.cn') > -1 ? api.wxLoginLocalUrl : Cookies.get("IS_DEV") ? api.wxLoginDevUrl : api.wxLoginUrl) + path + '?params=' + encodeURIComponent(JSON.stringify(query))

      let _path = baseUrl.wxAuthorization[window.ENV.env] + baseUrl.href[window.ENV.env] + path + '?params=' + encodeURIComponent(JSON.stringify(query))
      let params = {
        path: _path
      };
      axios.post(api.admin.get_auth_path, params).then(({
        data
      }) => {
        if (typeof(data.code) != "undefined" && data.code == 0) {
          location.href = data.result;
        } else {
          next()
        }
      }).catch(err => {
        Toast('')
        return;
      })

    } else {
      next()
    }
  } else {
    next()
  }
})

export default router